export default function chartmaker(name, data) {
  if (name === '막대그래프') {
    return (
      <div>
        <h2>CHART</h2>
        <Bar
          data={data}
          /* width={100}
          height={50} */
          options={{
            maintainAspectRatio: false,
          }}
        />
      </div>
    );
  } else if (name === '꺽은선그래프') {
    return (
      <div>
        <h2>CHART</h2>
        <Line
          data={data}
          /* width={100}
          height={50} */
          options={{
            maintainAspectRatio: false,
          }}
        />
      </div>
    );
  } else if (name === '원그래프') {
    return (
      <div>
        <h2>CHART</h2>
        <Doughnut
          data={data}
          /* width={100}
          height={50} */
          options={{
            maintainAspectRatio: false,
          }}
        />
      </div>
    );
  } else {
    return (
      <div>
        <h2>chart</h2>
      </div>
    );
  }
}
